
import { useState } from "react";
import React from "react";

const CompoundInterest = () => {
    const [principal, setPrincipal] = useState();
    const [interest, setInterest] = useState();
    const [years, setTime] = useState();
    const [result2, setResult] = useState();

    const calculate = () => {

        const result1 = Math.pow(1+ interest / 100 / 12, -years*12);
        const x = principal.split (",").join ("");
        const result = (parseInt(x));
        const result2 = result * ((interest / 100 / 12)/ (1-result1));

        setResult(result2.toLocaleString('en-IN', {
            maximumFractionDigits: 0,
            currency: 'INR'
          }));

        // PV*(r/12)/(1-(1+r/12)^(-n*12))

    };

    const updatePrincipal = (value)=>{
        const x = value.split (",").join ("");
        const result = (parseInt(x).toLocaleString('en-IN', {
            maximumFractionDigits: 0,
            currency: 'INR'
          }));
          setPrincipal (result);
    }
    return (
        <div className="row">
            <div className="col-lg-8">
                <form>
                    <div className="mb-3 row">
                        <label for="exampleFormControlInput1" className=" col-sm-4 calc-label">Loan Amount in ₹</label>
                        <div className="col-sm-8">
                            <input value={principal} onChange={(e) => updatePrincipal(e.target.value)} type="text" className="calc-control" id="exampleFormControlInput1" placeholder="" />
                        </div>
                    </div>
                    <div className="mb-3 row">
                        <label for="exampleFormControlInput1" className="col-sm-4 calc-label">Annual Interest in %</label>
                        <div className="col-sm-8">
                            <input onChange={(e) => setInterest(e.target.value)} type="number" className="calc-control" id="exampleFormControlInput1" placeholder="" />
                        </div>
                    </div>
                    <div className="mb-3 row">
                        <label for="exampleFormControlInput1" className=" col-sm-4 calc-label">Years</label>
                        <div className="col-sm-8">
                            <input onChange={(e) => setTime(e.target.value)} type="number" className="calc-control" id="exampleFormControlInput1" placeholder="" />
                        </div>
                    </div>
                    <button onClick={() => { calculate(); }} type="button" className="btn btn-primary">Calculate</button>
                </form>
            </div>
            <div className="col-lg-4">
                <div className="pricipalDiv">
                    <h3>Details</h3>
                    <h4>EMI (Rs): {result2}</h4>
                </div>
            </div>
        </div>

    )
}

export default CompoundInterest
